import Link from "next/link";
import Image from "next/image";
import { MapPin, Gauge, Calendar, Fuel, Heart, Eye, TrendingUp } from "lucide-react";
import type { Car } from "@/lib/data";

interface CarCardProps {
    car: Car;
}

export default function CarCard({ car }: CarCardProps) {
    return (
        <Link
            href={`/occasions/${car.id}`}
            className="group relative bg-white rounded-2xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-500 hover:-translate-y-3 border border-gray-100"
        >
            {/* Image avec overlay gradient */}
            <div className="relative h-52 overflow-hidden">
                <Image
                    src={car.image}
                    alt={`${car.brand} ${car.model}`}
                    fill
                    className="object-cover group-hover:scale-110 transition-transform duration-700"
                />

                {/* Gradient overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-black/20 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500" />

                {/* Badges */}
                <div className="absolute top-3 left-3 flex flex-col gap-2">
                    {car.isNew && (
                        <span className="px-3 py-1.5 bg-gradient-to-r from-green-500 to-emerald-600 text-white text-xs font-bold rounded-full shadow-lg flex items-center gap-1">
                            <TrendingUp className="w-3 h-3" />
                            NEUVE
                        </span>
                    )}
                    <span className="px-3 py-1.5 bg-black/70 backdrop-blur-sm text-white text-xs font-medium rounded-full">
                        {car.date}
                    </span>
                </div>

                {/* Action buttons */}
                <div className="absolute top-3 right-3 flex flex-col gap-2 opacity-0 group-hover:opacity-100 transition-all duration-300 transform translate-x-4 group-hover:translate-x-0">
                    <button className="w-10 h-10 bg-white/90 backdrop-blur-sm rounded-full flex items-center justify-center hover:bg-[#D32F2F] hover:text-white transition-colors shadow-lg">
                        <Heart className="w-4 h-4" />
                    </button>
                    <button className="w-10 h-10 bg-white/90 backdrop-blur-sm rounded-full flex items-center justify-center hover:bg-[#D32F2F] hover:text-white transition-colors shadow-lg">
                        <Eye className="w-4 h-4" />
                    </button>
                </div>

                {/* Barre de progression au hover */}
                <div className="absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r from-[#D32F2F] to-[#B71C1C] transform scale-x-0 group-hover:scale-x-100 transition-transform duration-500 origin-left" />
            </div>

            {/* Content */}
            <div className="p-5">
                {/* Titre */}
                <div className="mb-3">
                    <h3 className="font-bold text-lg text-gray-900 group-hover:text-[#D32F2F] transition-colors duration-300 line-clamp-1">
                        {car.brand} <span className="font-normal">{car.model}</span>
                    </h3>
                    <div className="flex items-baseline gap-2 mt-1">
                        <p className="text-2xl font-bold bg-gradient-to-r from-[#D32F2F] to-[#B71C1C] bg-clip-text text-transparent">
                            {car.price}
                        </p>
                    </div>
                </div>

                {/* Specs grid */}
                <div className="grid grid-cols-2 gap-2 mb-4">
                    <div className="flex items-center gap-1.5 text-xs text-gray-600 bg-gray-50 rounded-lg px-2 py-1.5">
                        <MapPin className="w-3.5 h-3.5 text-[#D32F2F]" />
                        <span className="font-medium">{car.city}</span>
                    </div>
                    <div className="flex items-center gap-1.5 text-xs text-gray-600 bg-gray-50 rounded-lg px-2 py-1.5">
                        <Gauge className="w-3.5 h-3.5 text-[#D32F2F]" />
                        <span className="font-medium">{car.km}</span>
                    </div>
                    <div className="flex items-center gap-1.5 text-xs text-gray-600 bg-gray-50 rounded-lg px-2 py-1.5">
                        <Calendar className="w-3.5 h-3.5 text-[#D32F2F]" />
                        <span className="font-medium">{car.year}</span>
                    </div>
                    <div className="flex items-center gap-1.5 text-xs text-gray-600 bg-gray-50 rounded-lg px-2 py-1.5">
                        <Fuel className="w-3.5 h-3.5 text-[#D32F2F]" />
                        <span className="font-medium">{car.fuel}</span>
                    </div>
                </div>

                {/* Tags */}
                {car.transmission && (
                    <div className="flex gap-2 pt-3 border-t border-gray-100">
                        <span className="px-3 py-1 bg-gradient-to-r from-gray-100 to-gray-50 text-gray-700 text-xs font-semibold rounded-full border border-gray-200">
                            {car.year}
                        </span>
                        <span className="px-3 py-1 bg-gradient-to-r from-gray-100 to-gray-50 text-gray-700 text-xs font-semibold rounded-full border border-gray-200">
                            {car.transmission}
                        </span>
                    </div>
                )}

                {/* Hover indicator */}
                <div className="absolute bottom-0 left-0 right-0 h-0.5 bg-gradient-to-r from-[#D32F2F] via-[#F77F88] to-[#D32F2F] transform scale-x-0 group-hover:scale-x-100 transition-transform duration-500" />
            </div>
        </Link>
    );
}
