import Link from "next/link";
import Image from "next/image";
import { MapPin, Car, Phone, Star, ArrowRight, Building2 } from "lucide-react";
import type { Showroom } from "@/lib/data";

interface ShowroomCardProps {
    showroom: Showroom;
}

export default function ShowroomCard({ showroom }: ShowroomCardProps) {
    return (
        <Link
            href={`/concessionnaires/${showroom.id}`}
            className="group relative bg-white rounded-2xl overflow-hidden shadow-lg hover:shadow-2xl transition-all duration-500 hover:-translate-y-2"
        >
            {/* Image avec overlay */}
            <div className="relative h-56 overflow-hidden">
                <Image
                    src={showroom.image}
                    alt={showroom.name}
                    fill
                    className="object-cover group-hover:scale-110 transition-transform duration-700"
                />

                {/* Gradient overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-black/40 to-transparent" />

                {/* Rating badge */}
                {showroom.rating && (
                    <div className="absolute top-4 right-4 flex items-center gap-1.5 px-3 py-2 bg-white/95 backdrop-blur-sm rounded-full shadow-lg">
                        <Star className="w-4 h-4 text-yellow-500 fill-yellow-500" />
                        <span className="text-sm font-bold text-gray-900">{showroom.rating}</span>
                    </div>
                )}

                {/* Cars count badge */}
                <div className="absolute bottom-4 left-4 flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-[#D32F2F] to-[#B71C1C] text-white rounded-full shadow-lg">
                    <Car className="w-4 h-4" />
                    <span className="text-sm font-bold">{showroom.cars} voitures</span>
                </div>

                {/* Icon overlay */}
                <div className="absolute top-4 left-4 w-12 h-12 bg-white/10 backdrop-blur-md rounded-full flex items-center justify-center opacity-0 group-hover:opacity-100 transition-all duration-300 transform -translate-y-2 group-hover:translate-y-0">
                    <Building2 className="w-6 h-6 text-white" />
                </div>
            </div>

            {/* Content */}
            <div className="p-6">
                {/* Title */}
                <h3 className="text-xl font-bold text-gray-900 group-hover:text-[#D32F2F] transition-colors duration-300 mb-3 line-clamp-1">
                    {showroom.name}
                </h3>

                {/* Info */}
                {showroom.address && showroom.phone && (
                    <div className="space-y-3 mb-4">
                        <div className="flex items-start gap-3 text-gray-600 text-sm">
                            <div className="w-8 h-8 bg-gradient-to-br from-[#D32F2F]/10 to-[#B71C1C]/10 rounded-lg flex items-center justify-center flex-shrink-0">
                                <MapPin className="w-4 h-4 text-[#D32F2F]" />
                            </div>
                            <span className="line-clamp-2 leading-relaxed">{showroom.address}</span>
                        </div>

                        <div className="flex items-center gap-3 text-gray-600 text-sm">
                            <div className="w-8 h-8 bg-gradient-to-br from-[#D32F2F]/10 to-[#B71C1C]/10 rounded-lg flex items-center justify-center flex-shrink-0">
                                <Phone className="w-4 h-4 text-[#D32F2F]" />
                            </div>
                            <span className="font-medium">{showroom.phone}</span>
                        </div>
                    </div>
                )}

                {/* Footer */}
                <div className="flex items-center justify-between pt-4 border-t border-gray-100">
                    <div className="flex items-center gap-2 px-3 py-1.5 bg-gradient-to-r from-gray-50 to-gray-100 rounded-full">
                        <span className="text-lg">üìç</span>
                        <span className="text-sm font-semibold text-gray-700">{showroom.city}</span>
                    </div>

                    <div className="flex items-center gap-2 text-[#D32F2F] font-semibold text-sm group-hover:gap-3 transition-all duration-300">
                        <span>D√©tails</span>
                        <ArrowRight className="w-4 h-4 group-hover:translate-x-1 transition-transform" />
                    </div>
                </div>
            </div>

            {/* Hover border effect */}
            <div className="absolute inset-0 border-2 border-[#D32F2F] rounded-2xl opacity-0 group-hover:opacity-100 transition-opacity duration-300 pointer-events-none" />
        </Link>
    );
}
